import React, { Component } from 'react';

import './App.css';
import movieRow from './MovieRow.js'
import MovieRow from './MovieRow.js';
import $ from 'jquery'

class App extends Component {
  constructor(props){
    super(props)
    this.state = {}
    // console.log("this is my initializer")

    // const movies = [
    //   {id: 0, poster_src:"https://images-na.ssl-images-amazon.com/images/I/A1t8xCe9jwL._SY679_.jpg", title: "Avengers: Infinity War", overview: "As the Avengers and their allies..."},
    //   {id: 1, poster_src:"https://vignette.wikia.nocookie.net/marvelcinematicuniverse/images/c/ca/Avengers_Infinity_War_Imax_poster.jpg/revision/latest?cb=20180405170202", title: "The Avengers", overview: "When an unexpected enemy emerges and threatens..."}
    // ]

    

    // var movieRows = []
    //  movies.forEach((movie) => {
    //  console.log(movie.id)
    //  const movieRow = <MovieRow movie={movie} />
    
    // movieRows.push(movieRow)
    // })

    // this.state = {rows: movieRows}
    this.performSearch("ant man")
  }

  performSearch(searchTerm){
    console.log("Perform search using movieDB")
    const urlString ="https://api.themoviedb.org/3/search/movie?api_key=5d973f9b2c35226d6846f0700f5a63c6&query=" + searchTerm
    $.ajax({
      url: urlString,
      success: (searchResults) =>{
        console.log("Successfully fetched Data")
        const results = searchResults.results
        console.log(results[0])

        var movieRows = []

        results.forEach((movie) => {
          movie.poster_src = 'https://image.tmdb.org/t/p/w185' + movie.poster_path
          // console.log(movie.poster_path)
          const movieRow = <MovieRow key={movie.id} movie={movie}/>
          movieRows.push(movieRow)
        })

        this.setState({rows: movieRows})
      },
      error: (xhr, status, err) =>{
        console.error("failed to fetch data")
      }
    })
  }
  searchChangeHandler(event){
    console.log(event.target.value)
    const boundObject = this
    const searchTerm = event.target.value
    boundObject.performSearch(searchTerm)
  }
  render() {
    return (
      <div >
        <table className="titleBar">
          <tbody>
            <tr>
              <td>
                <img alt="app icon" width="50"src='https://res.cloudinary.com/bobbynicholsoncloud/image/upload/v1529004827/new/movieDb.svg'/>
              </td>
              <td width="8"/>
              <td>
                <h1>MoviesDB Search</h1>
              </td>
            </tr>
          </tbody>
        </table>
        <input style={{
          fontSize: 24,
          display: 'block',
          width: "99%",
          paddingTop: 8,
          paddingBottom: 8,
          paddingLeft: 16
        }} onChange={this.searchChangeHandler.bind(this)}placeholder="Enter search term"/>
        {this.state.rows}
      </div>
    );
  }
}

export default App;



